"use client";
import { useState } from 'react';
import axios from 'axios';

export default function TrackTicket() {
  const [ticketId, setTicketId] = useState('');
  const [ticketData, setTicketData] = useState(null);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSearch = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setTicketData(null);

    try {
      const res = await axios.get(`http://127.0.0.1:8000/api/status/${ticketId}/`);
      setTicketData(res.data);
    } catch (err) {
      setError("Ticket not found. Please check the ID and try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col items-center justify-center p-6">
      <div className="bg-white p-8 rounded-lg shadow-md max-w-md w-full">
        <h1 className="text-2xl font-bold mb-4 text-center text-gray-800">Track Your Ticket</h1>

        {/* Search Box */}
        <form onSubmit={handleSearch} className="flex gap-2 mb-6">
          <input 
            type="number" 
            placeholder="Enter Ticket ID (e.g., 1)" 
            value={ticketId}
            onChange={(e) => setTicketId(e.target.value)}
            className="flex-1 p-2 border border-gray-300 rounded focus:outline-none focus:border-blue-500"
            required
          />
          <button 
            type="submit" 
            disabled={loading}
            className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700 transition disabled:bg-gray-400"
          >
            {loading ? '...' : 'Track'}
          </button>
        </form>

        {/* Error Message */}
        {error && <p className="text-red-500 text-center mb-4">{error}</p>}

        {/* Result Display */}
        {ticketData && (
          <div className="border-t pt-4 space-y-3">
            <div className="flex justify-between items-center">
              <span className="text-gray-500 text-sm">Status</span>
              <span className={`px-3 py-1 rounded-full text-sm font-bold 
                ${ticketData.status === 'Open' ? 'bg-yellow-100 text-yellow-700' : 
                  ticketData.status === 'Assigned' ? 'bg-blue-100 text-blue-700' : 
                  ticketData.status === 'Resolved' ? 'bg-green-100 text-green-700' : 
                  'bg-gray-200 text-gray-700'}`}>
                {ticketData.status}
              </span>
            </div>

            <div>
              <span className="block text-gray-500 text-sm">Issue Type</span>
              <span className="font-medium">{ticketData.issue_type}</span>
            </div>

            <div>
              <span className="block text-gray-500 text-sm">Description</span>
              <p className="text-gray-700 bg-gray-50 p-2 rounded text-sm mt-1">
                {ticketData.description}
              </p>
            </div>

            {/* Show Assigned Member only if assigned */}
            {ticketData.assigned_member_display && (
              <div className="bg-blue-50 p-3 rounded mt-2 border border-blue-100">
                <span className="block text-blue-500 text-xs uppercase font-bold tracking-wide">Assigned Agent</span>
                <span className="font-semibold text-blue-900">{ticketData.assigned_member_display}</span>
              </div>
            )}
            
            {/* Show Submission Date */}
             <div className="text-xs text-gray-400 text-center mt-4">
              Submitted on: {new Date(ticketData.created_at).toLocaleString()}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}